import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';

import { UserWallet } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import {
  AppNameService,
  EventbusService, MainBarTitleService, UserWalletListBase,
  WindowService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';
import {
  AdminDateFilterFieldComponent
} from '../../../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalContainerComponent } from '../../../../misc/admin-modal-container/admin-modal-container.component';
import { AdminModalComponent } from '../../../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../../../misc/admin-top-bar/admin-top-bar.component';
import { AddUpdateUserWalletComponent } from '../add-update-user-wallet/add-update-user-wallet.component';
import { ViewUserWalletComponent } from '../view-user-wallet/view-user-wallet.component';

@Component({
  selector: 'app-user-wallet-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminModalComponent,
    AdminTableComponent,
    AdminPageContentComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent,
    AdminModalContainerComponent,
    ViewUserWalletComponent,
    AddUpdateUserWalletComponent
  ],
  templateUrl: './user-wallet-list.component.html',
  styleUrl: './user-wallet-list.component.scss'
})
export class UserWalletListComponent
  extends UserWalletListBase
  implements OnInit, OnDestroy {
  deleteModalOptions = {
    showModal: false,
    modalTitle: 'Remove User Wallet',
    modalMsg: 'Are you sure you want to remove this item?',
    modalCancelButton: true,
    modalCancelText: 'Cancel',
    modalConfirmText: 'Remove',
    modalConfirmButton: true
  };

  updateModalOptions = {
    showModal: false,
    modalTitle: 'Update User Wallet',
    modalMsg: 'Are you sure you want to update this item?',
    modalCancelButton: true,
    modalCancelText: 'Cancel',
    modalConfirmText: 'Update',
    modalConfirmButton: true
  };

  viewModalOptions = {
    showModal: false,
    modalTitle: 'View User Wallet',
    modalMsg: 'Are you sure you want to view this item?',
    modalCancelButton: true,
    modalCancelText: 'Cancel',
    modalConfirmText: 'View',
    modalConfirmButton: true
  };

  userWallet?: UserWallet;

  constructor(
    protected override windowService: WindowService,
    protected override toastr: ToastrService,
    protected override router: Router,
    protected override eventbus: EventbusService,
    protected override apNme: AppNameService,
    public override mainBarTitle: MainBarTitleService,
    protected override metaTagService: Meta,
    protected override titleService: Title
  ) {
    super(windowService, toastr, router, eventbus, apNme, mainBarTitle, metaTagService, titleService);
  }

  ngOnInit(): void {
    this.init();
  }

  ngOnDestroy(): void {
    this.destroy();
  }

  viewUserWallet(userWallet: UserWallet): void {
    this.eventbus.generalEvent$.next({
      type: 'viewUserWallet',
      data: userWallet
    });
    this.userWallet = userWallet;
    this.viewModalOptions.showModal = true;
  }

  updateUserWallet(userWallet: UserWallet): void {
    this.eventbus.generalEvent$.next({
      type: 'updateUserWallet',
      data: userWallet
    });
    this.userWallet = userWallet;
    this.updateModalOptions.showModal = true;
  }

  deleteUserWallet(userWallet: UserWallet): void {
    this.userWallet = userWallet;
    this.deleteModalOptions.showModal = true;
  }

  toggleModalDelete(): void {
    this.deleteModalOptions.showModal = !this.deleteModalOptions.showModal;
  }

  toggleModalUpdate(): void {
    this.updateModalOptions.showModal = !this.updateModalOptions.showModal;
  }

  toggleModalView(): void {
    this.viewModalOptions.showModal = !this.viewModalOptions.showModal;
  }

  async onModalDeleteConfirm(event: boolean) {
    if (!event) {
      return;
    }

    try {
      await this.userWallet?.remove();
      this.toastr.success('User Wallet deleted successfully');
      this.deleteModalOptions.showModal = false;
      this.listCompose.getDataArr();
    } catch (error) {
      FrontendLogger.error(error);
      this.toastr.error('Failed to remove user wallet');
    }
  }
}
