import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import { EventbusService, openClose, ViewUserWalletBase } from '@front-shared-lib';

@Component({
  selector: 'app-view-user-wallet',
  standalone: true,
  imports: [
    RouterModule
  ],
  animations: [
    openClose
  ],
  templateUrl: './view-user-wallet.component.html',
  styleUrl: './view-user-wallet.component.scss'
})
export class ViewUserWalletComponent
  extends ViewUserWalletBase
  implements OnInit, OnDestroy {
  constructor(
    protected override router: Router,
    protected override route: ActivatedRoute,
    protected override eventbus: EventbusService
  ) {
    super(router, route, eventbus);
  }

  ngOnInit(): void {
    this.init();
  }

  ngOnDestroy(): void {
    this.destroy();
  }

  closeModal(): void {
    this.eventbus.generalEvent$.next({
      type: 'closeViewUserWalletModal'
    });
  }
}
